import React from 'react'
import Team from '../assets/team.jpg'

const TeamPage = () => (
    <div style={{
        fontFamily: 'Raleway',
        fontSize: '18px',
        color: '#F9CF87',
        textShadow: '1px 1px black',
        height:'100%',
    }}>
    <h1>ShovelBaconBurger</h1>
    <p>The team behind <b>Leaf</b></p>
        <div style={{
            height:'400px',
        }}>
            <img src={Team} />
        <p>Left to right: Miikka Oksanen, Harri Mähönen, Alex Mäkinen</p>
        </div>
  </div>
)

export default TeamPage



